.App-header { 
    display: flex;  

}
.Master-header{
    box-shadow: 0px 0px 20px 1px rgba(0, 0, 0, 0.020);
    min-height: 100px;  
    display: flex;
    justify-content: center;
    
    color: white;
    letter-spacing: 1px;
    position: fixed;
    width: 100%;
    background-color: white;
    z-index: 1000;
}
.Header-body{
    display: flex;
    width: 1000px;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    text-shadow: 0px 5px 10px rgba(0, 0, 0, 0.20);
    font-size: 20px;
}


.Header-left{
    display: flex;
    align-items: center;
    user-select: none;
}
.Header-logo{
    font-size: 25px;
    font-weight: 600;
    padding: 10px;
}
.Header-menu{
    margin: 0px;
    margin-left:20px ;

}
.Menu-item{
    padding: 10px;
    margin: 0px 10px;
    opacity: 0.7;
    transition: 0.2s;
    
    
}
.Menu-item.active{
    opacity: 2;
    font-weight: 600;
    color: #725eff;
    text-shadow: 0px 5px 10px #715eff50;
}
.Menu-item:hover{
    opacity: 2;
    color: #725eff;
    font-weight: 600;
    transition: 0.2s;
    
}

.User-action{
    display: flex;
    align-items: center;
}
.User-action-item{
    margin-left: 10px;
   padding: 8.25px 10px;
   opacity: 0.7;
    font-size: 20px;

}
.User-action-item.active{
    opacity: 2;
    border-radius: 100px;
    background-color: rgba(0, 0, 0, 0.040);
    
}

.User-action-item:hover{
    border-radius: 100px;
    background-color: rgba(0, 0, 0, 0.040);
    opacity: 2;
}

.User-action-item.IconSearchOutlined{
    display: none;
    padding: 10px;
}

/*/search/*/
.Header-search{
    display: flex;
    align-items: center;
    opacity: 0.5;
}
.Header-search:hover{
    opacity: 2;
}
input{
    border-radius: 20px;
    height: 40px;
    width: 230px;
    letter-spacing: 1px;
    padding-left: 40px;
    border-width: 0px;
    background-color: rgba(0, 0, 0, 0.030);
    transition: 0.5s;
    box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.50);
    transition: 0.2s;
}
input:hover{
    box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.90);
    transition: 0.3s;
}

.Search-icon{
    color: black;
    font-size: 20px;
  
    position: relative;
    left: 30px;
    transition: 0.4s;
}

.second-nav{
    display: flex;
    justify-content: center;
    overflow: auto;
    width: 100%;
    align-items: center;
    margin-top: 100px;
    box-shadow: inset 0px 0px 10px #00000010;
}
.second-menu{
    
    margin: 0px;
    display: flex;
    min-width: 600px;
    width: 1000px;
    
    
    padding-left:20px;
}
.second-menuitem{
    
   user-select: none;
    font-size: 15px;
    padding: 2px 10px;
    margin: 0px 10px 0px 0px;
    opacity: 0.7;
    line-height: 50px;
}
.second-menuitem:hover{
    opacity: 2;
    font-weight: 500;
    
    color: #725eff;
}
.second-menuitem.active{
    opacity: 2;
    font-weight: 500;
    border-bottom: 2px solid #725eff;
    color: #725eff;
}



main{
    display: flex;
    flex-direction: column;
}


.devloping{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 50vh;
    background-color: #00000000;
    width: 100%;
}


@media screen and (max-width:810px) {
    .Header-body{
        font-size: 18px;
    }
    input{
        width: 150px;
        transition: 0.5s;
    }
   
}

@media screen and (max-width:695px) {
   .Master-header{
    min-height: 60px;
   }
    .Header-menu{
        display: none;
    }
    
    .User-action-item.icon{
        display: none;
    }
    .User-action-item{
        margin-left: 0px;
    }

    .User-action-item.IconSearchOutlined{
        display: flex;
    }

    .Header-search{
        display: none;
    }
    .second-nav{
        margin-top: 70px;
        display: block;
    }
    .second-menu{
        width: 150%;
    }
    
}