.modalstyle{
    position: absolute;
    top: 50%;
    left: 50%;
    transform:translate(-50%, -50%) ;
    width: 400px;
    background-color: white;
    border-radius: 10px;
    box-shadow: -10px 11px 15px -7px rgb(0 0 0 / 20%), 0px 24px 38px 3px rgb(0 0 0 / 14%), 0px 9px 46px 8px rgb(0 0 0 / 12%);
    padding:16px 15px 24px ;
    
}

.loginTitle{
    display: flex;
    align-items:center;
    justify-content: space-between;
    opacity: 0.8;
    margin: 0px 0px;
   
}
.loginTitleName{
    margin: 0px;
    padding:  10px;
}
.closeIcon{
    font-size: 20px;
   opacity: 0.5;
   border-radius: 20px;
   padding: 8.25px 10px;
}
.closeIcon:hover{
    opacity: 2;
    background-color: rgba(0, 0, 0, 0.050);
}

.loginMain{
    display: flex;
   align-items: center;
   flex-direction: column;
   justify-content: center;
    margin: 200px 0px;
    opacity: 0.7;
}
.loginMainItem{
    margin: 10px 5px;
}

