
.firstpage_section{
display: flex;
width: 100%;
justify-content: center;
}

.fs_s1{
    display: flex;
  
    width: 1000px;
    padding: 30px 10px;
}
.fs_s1_1{
    display: flex;
   width: 100%;
    padding: 0px 10px;
    justify-content: space-between;
}

.s1_pic{
    display: flex;
    background-image: linear-gradient(100deg, #fffffe 0%, #e7ecf7 120%);
    width: 72%;
    border-radius: 10px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.150);
    transition: 0.2s;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}
.s1_pic:hover{
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.150);
    transition: 0.4s;
    
}
.s1_event{
    display: flex;
    background-image: linear-gradient(145deg, #fffffe 0%, #e7ecf7 120%); 
    width: 26%;
    border-radius: 10px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.150);
    transition: 0.2s;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}
.s1_event:hover{
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.150);
    transition: 0.4s;
}

.imgbox{
    width: 100%;
    transition: 0.3s;
}
.imgbox:hover{
   
    transform: scale(1.05);
    overflow: hidden;
    transition: 0.3s;
}

@media screen and (max-width:695px) {
    .fs_s1_1{
        flex-direction: column;
    }
    .s1_pic{
        width: 100%;
    }
}